.first {
  background: url(../img/00.jpg) no-repeat center center/cover;
}

.header {
  display: flex;
  flex-direction: column;
}

.header__about {
  display: flex;
  justify-content: space-between;
  margin-bottom: 3.125rem;
  align-items: center;
}

.header__logo {
  font-size: 5.625rem;
  display: inline-block;
  background: linear-gradient(315deg, #d3d3d3 0%, #57606f 80%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.header__logo:hover {
  background: #FFD700;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: all 0.6s linear;
}

.header__right {
  display: flex;
  align-items: center;
}

.header__contacts {
  display: -webkit-box;
  align-items: center;
}

.header__contacts--phone {
  display: flex;
  font-size: 1.5rem;
  margin-right: 2.625rem;
  font-family: Museo Sans Cyrl 300;
  line-height: 1.8125rem;
  text-align: right;
}

.header__contacts--phone:hover {
  opacity: 0.8;
}

.header__contacts--phone:hover .phone {
  opacity: 1;
  fill: #FFD700;
}

.header__title {
  background: linear-gradient(315deg, #d3d3d3 0%, #57606f 80%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 3.75rem;
  display: inline-block;
}

.header__subtitle {
  font-size: 3.125rem;
  line-height: 3.75rem;
  font-weight: bold;
  margin-bottom: 3.125rem;
}

.header__title br {
  display: none;
}

.header__desc {
  display: flex;
  flex-direction: column;
}

.header__desc-info {
  display: inline-block;
}

.header__desc-info h1 {
  overflow: hidden;
  border-right: 0.17em solid #e29508;
  white-space: nowrap;
  margin: 0 auto;
  animation: typing 3s steps(30, end), blink-caret 0.7s step-end infinite;
}

@keyframes typing {
  from {
    width: 0;
  }

  to {
    width: 45%;
  }
}

@keyframes blink-caret {
  from, to {
    border-color: transparent;
  }

  50% {
    border-color: orange;
  }
}

.phone {
  margin-right: 1.3125rem;
  fill: #fff;
}

.instagram {
  background: radial-gradient(98.67% 98.67% at -7.7% 73.44%, #ffdd55 0%, #ffdd55 10%, #ff543e 50%, #c837ab 100%);
  margin-right: 1.75rem;
}

.vk {
  margin-right: 3.5rem;
}

.header__burger {
  position: relative;
  width: 1.5625rem;
  height: 0.875rem;
}

.header__bottom {
  display: inline-flex;
  flex-direction: column;
}

.adv__list {
  margin-bottom: 6.25rem;
}

.adv__item {
  cursor: pointer;
  padding-bottom: 0.9375rem;
  display: flex;
  align-items: center;
  padding: 0.625rem 0.625rem;
  background: transparent;
  height: 3.125rem;
}

.adv__item.active .adv__item--title {
  background: linear-gradient(315deg, #d3d3d3 0%, #57606f 80%);
  border-radius: 0.25rem;
  font-size: 2.25rem;
  font-weight: bold;
  transition: all ease-in-out 0.5s;
}

.adv__item:hover .adv__item--title {
  opacity: 0.5;
}

.adv__item--images {
  display: flex;
  align-items: center;
  margin-right: 2.5rem;
}

.adv__item--title {
  font-size: 1.5rem;
  font-weight: bold;
}

.gold__text {
  color: #FFD700;
}

.big__btn {
  font-size: 1.1875rem;
  line-height: 1.4375rem;
  text-align: center;
  letter-spacing: 0.155em;
  text-transform: uppercase;
  background: linear-gradient(315deg, #d3d3d3 0%, #57606f 80%);
  border: 1px solid transparent;
  box-sizing: border-box;
  border-radius: 0.25rem;
  color: #FFD700;
  font-weight: bold;
  padding: 0.9375rem 4.375rem;
  margin-bottom: 3.125rem;
  width: 16.625rem;
}

.big__btn:hover {
  opacity: 0.5;
  border: 1px solid #FFD700;
  transition: all 0.8s linear;
}

.block__arrow {
  text-align: center;
}

.block__arrow--link {
  text-align: center;
  display: inline-block;
}

.arrow__link {
  -webkit-overflow-scrolling: touch;
}

.arrow__link:hover {
  position: relative;
  bottom: -0.9375rem;
  animation: bounce 1.3s infinite;
}

@keyframes bounce {
  0% {
    bottom: 0;
  }

  100% {
    bottom: -0.9375rem;
  }
}

.link__down {
  fill: #FFD700;
  transform: rotate(90deg);
}

* {
  box-sizing: border-box;
}

html {
  font-size: 16px;
  color: white;
}

body,
html {
  height: 100%;
}

body {
  font-family: "Museo Sans Cyrl 300";
}

.body__overflow {
  overflow: hidden;
}

.wrapper {
  overflow: hidden;
  min-height: 100%;
  width: 100%;
  position: relative;
}

section {
  width: 100%;
  height: 100%;
  padding: 1.5625rem 0;
}

.container {
  padding: 0 5.5rem;
}

button {
  border: 0;
  cursor: pointer;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
  margin: 0;
}

h1 {
  font-size: 5.625rem;
  text-transform: uppercase;
  font-family: "Bebas Neue";
  font-weight: bold;
  font-style: normal;
  line-height: 5.625rem;
  margin-bottom: 0.625rem;
}

h2 {
  font-family: "Bebas Neue";
  font-size: 4.375rem;
  line-height: 4.375rem;
  text-align: center;
  text-transform: uppercase;
  background: linear-gradient(315deg, #d3d3d3 0%, #57606f 80%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 1.25rem;
}

p {
  margin: 0;
  padding: 0;
  font-size: 1.125rem;
  line-height: 160%;
  color: #303030;
  margin-bottom: 1.25rem;
}

button[type="button"] {
  cursor: pointer;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

img {
  display: block;
}

@font-face {
  font-family: 'Bebas Neue';
  src: url("../fonts/BebasNeueBold.woff2") format("woff2"), url("../fonts/BebasNeueBold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'Museo Sans Cyrl 700';
  src: url("../fonts/MuseoSansCyrl-700.woff2") format("woff2"), url("../fonts/MuseoSansCyrl-700.woff") format("woff");
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: 'Museo Sans Cyrl 300';
  src: url("../fonts/MuseoSansCyrl-300.woff2") format("woff2"), url("../fonts/MuseoSansCyrl-300.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}

.header__burger {
  position: relative;
  width: 1.5625rem;
  height: 0.875rem;
}

.header__menu {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
  width: 3.125rem;
  height: 100%;
}

.header__menu--decor {
  display: inline-block;
  height: 2px;
  width: 3.125rem;
  border-radius: 3px;
  background-color: #fff;
}

.second {
  color: linear-gradient(315deg, #d3d3d3 0%, #57606f 80%);
  background: url("../img/12620.jpg") center center/no-repeat;
  padding: 4.375rem 0;
}

.slider {
  position: relative;
  overflow: hidden;
  max-width: 87.5rem;
  margin-left: auto;
  margin-right: auto;
  margin: 2.5rem 0;
}

.slider__wrapper {
  position: relative;
  overflow: hidden;
}

.slider__items {
  display: flex;
  transition: transform 0.6s ease;
}

.slider__item {
  flex: 0 0 100%;
  max-width: 100%;
  color: black;
  font-size: 1.25rem;
  margin-bottom: 1.25rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  overflow: hidden;
  justify-content: center;
}

.left__side {
  margin-right: 5rem;
  width: 50%;
  padding-left: 2.5rem;
}

.right__side {
  display: flex;
  align-items: center;
  width: 50%;
}

.slide__info {
  display: flex;
  flex-direction: column;
  margin-bottom: 2.4375rem;
}

.name {
  font-family: "Bebas Neue";
  font-size: 4.375rem;
  line-height: 4.375rem;
  text-transform: uppercase;
  color: #FFD700;
  opacity: 0.6;
  animation: blink 2s infinite;
}

@keyframes blink {
  from {
    opacity: 1;
  }

  to {
    opacity: 0.1;
  }
}

.slide__info--button {
  display: none;
}

.slider__slide--list {
  margin-bottom: 3.125rem;
  list-style: none;
}

.slider__slide--list--item {
  width: 21.875rem;
  color: gray;
  margin-bottom: 1.5625rem;
}

.slider__slide--list--item:before {
  content: "";
  background: linear-gradient(315deg, #d3d3d3 0%, #57606f 80%);
  width: 0.625rem;
  height: 0.625rem;
  border-radius: 0.1875rem;
  display: inline-block;
  line-height: 2rem;
  color: linear-gradient(315deg, #d3d3d3 0%, #57606f 80%);
  margin-right: 1.8125rem;
}

.slider__slide--list--desc {
  width: 90%;
}

.slider__slide--info--title {
  font-size: 1.125rem;
  color: #000000;
  font-weight: bold;
  margin-bottom: 0.625rem;
}

.slider__slide--info--text {
  font-size: 1.5625rem;
  line-height: 160%;
  color: #000000;
  font-weight: bold;
  text-decoration: underline;
}

.slider__slide--info--author {
  font-size: 1.25rem;
  line-height: 160%;
  color: #303030;
}

.slide__image {
  width: 100%;
  max-width: 40.625rem;
  height: auto;
}

.slider__control {
  position: absolute;
  top: 50%;
  display: none;
  align-items: center;
  justify-content: center;
  width: 2.5rem;
  color: #fff;
  text-align: center;
  opacity: 0.5;
  height: 3.125rem;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.5);
}

.slider__control_show {
  display: flex;
}

.slider__control:hover,
.slider__control:focus {
  color: #fff;
  text-decoration: none;
  outline: 0;
  opacity: 0.9;
}

.slider__control_prev {
  left: 0;
}

.slider__control_next {
  right: 0;
}

.slider__control::before {
  content: "";
  display: inline-block;
  width: 1.25rem;
  height: 1.25rem;
  background: transparent no-repeat center center;
  background-size: 100% 100%;
}

.slider__control_prev::before {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='0 0 8 8'%3E%3Cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E");
}

.slider__control_next::before {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='0 0 8 8'%3E%3Cpath d='M2.75 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E");
}

/* стили для индикаторов */

.slider__indicators {
  position: absolute;
  right: 0;
  bottom: -10px;
  left: 0;
  z-index: 15;
  display: flex;
  justify-content: center;
  padding-left: 0;
  margin-right: 15%;
  margin-left: 15%;
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
}

.slider__indicators li {
  box-sizing: content-box;
  flex: 0 1 auto;
  width: 30px;
  height: 4px;
  margin-right: 3px;
  margin-left: 3px;
  text-indent: -999px;
  cursor: pointer;
  background-color: #FFD700;
  background-clip: padding-box;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
}

.slider__indicators li.active {
  background-color: gray;
}

.img-fluid {
  display: inline-block;
  height: auto;
  max-width: 100%;
}

.partners {
  background: linear-gradient(180deg, #fdf4f4 0%, #ecf3ec 100%);
  padding: 3.75rem 0px;
  color: #303030;
}

.brand__list {
  display: flex;
  flex-wrap: wrap;
  color: black;
  justify-content: center;
}

.brand__elem {
  width: 26.875rem;
  margin: 3.8125rem 2.0375rem 3.75rem 0;
  border: 2px solid transparent;
}

.brand__elem:hover .brand__icon {
  border: 2px solid #FFD700;
}

.brand__icon {
  height: 15rem;
  box-shadow: 0rem 0rem 0.9375rem rgba(0, 0, 0, 0.15);
  margin-bottom: 1.625rem;
  transition: background 1s linear;
  border: 2px solid transparent;
}

.brand__icon[data="1"] {
  background: #fff;
  right: -9999;
}

.brand__icon[data="1"]:hover {
  background: url(../img/facebook.jpeg) center center;
  background-size: cover;
  background-repeat: no-repeat;
}

.brand__icon[data="2"] {
  background: #fff;
}

.brand__icon[data="2"]:hover {
  background: url(../img/amazon.jpg) center center;
  background-size: cover;
  background-repeat: no-repeat;
}

.brand__icon[data="3"] {
  background: #fff;
}

.brand__icon[data="3"]:hover {
  background: url(../img/tesla.png) center center;
  background-size: contain;
  background-repeat: no-repeat;
}

.brand__icon[data="4"] {
  background: #fff;
}

.brand__icon[data="4"]:hover {
  background: url(../img/ultra.jpg) center center;
  background-size: cover;
  background-repeat: no-repeat;
}

.brand__review {
  font-size: 1.125rem;
  line-height: 160%;
  font-weight: 700;
  margin-bottom: 0.625rem;
}

.order {
  background-image: linear-gradient(to right, #060b3b, #0c2756, #124271, #195e8c, #1f79a7, #2595c2, #379fa9, #49a88f, #5cb276, #6ebc5c, #80c543, #92cf29);
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 43.75rem;
  position: relative;
}

.form {
  width: 40.625rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 40.625rem;
  flex-grow: 1;
}

.form__title {
  background: linear-gradient(315deg, #d3d3d3 0%, #57606f 80%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 3.125rem;
}

.form__row {
  display: flex;
  padding: 0.4375rem 0.5rem;
  margin-bottom: 0.4375rem;
  justify-content: space-around;
  width: inherit;
}

form label {
  display: inline-block;
  font-size: 1.2em;
  font-weight: bold;
  width: 7.5rem;
  padding: 0.375rem 0;
  color: #464646;
  vertical-align: top;
}

.req {
  color: #ca5354;
}

.form__note {
  font-size: 1.2em;
  line-height: 1.33em;
  font-weight: normal;
  padding: 0.125rem 0.4375rem;
  margin-bottom: 0.625rem;
}

.form input {
  width: 25.75rem;
}

form input:focus,
.form__textarea:focus {
  outline: none;
}

