.nav__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  background-color: black;
  /* Firefox */
  background-image: -moz-radial-gradient(circle, #ffffff 2px, rgba(248, 255, 128, 0.5) 4px, transparent 40px), -moz-radial-gradient(circle, #ffffff 1px, rgba(255, 186, 170, 0.4) 3px, transparent 30px), -moz-radial-gradient(ellipse, rgba(255, 255, 255, 0.9) 1px, rgba(251, 255, 186, 0.3) 2px, transparent 40px), -moz-radial-gradient(ellipse, rgba(255, 255, 255, 0.4), rgba(253, 255, 219, 0.2) 1px, transparent 30px);
  /* Webkit */
  background-image: -webkit-gradient(radial, 50% 50%, 2, 50% 50%, 40, from(white), color-stop(0.1, rgba(248, 255, 128, 0.5)), to(transparent)), -webkit-gradient(radial, 50% 50%, 1, 50% 50%, 30, from(white), color-stop(0.1, rgba(255, 186, 170, 0.4)), to(transparent)), -webkit-gradient(radial, 50% 50%, 1, 50% 50%, 40, from(rgba(255, 255, 255, 0.9)), color-stop(0.05, rgba(251, 255, 186, 0.3)), to(transparent)), -webkit-gradient(radial, 50% 50%, 0, 50% 50%, 30, from(rgba(255, 255, 255, 0.4)), color-stop(0.03, rgba(253, 255, 219, 0.2)), to(transparent));
  /* Background Attributes */
  background-size: 550px 550px, 350px 350px, 250px 270px, 220px 200px;
  background-position: 0 0, 30px 60px, 130px 270px, 70px 150px;
  /* Animation */
  animation-name: movement;
  animation-duration: 5s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  /* Firefox */
  -moz-animation-name: movement;
  -moz-animation-duration: 5s;
  -moz-animation-timing-function: linear;
  -moz-animation-iteration-count: infinite;
  /* Webkit */
  -webkit-animation-name: movement;
  -webkit-animation-duration: 5s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
}

@keyframes movement {
  from {
    background-position: 0 0, 30px 60px, 130px 270px, 70px 150px;
  }

  to {
    background-position: -550px 0, -320px 60px, -120px 270px, -150px 150px;
  }
}

@-moz-keyframes movement {
